@media (max-width: 1186px) {
    .path {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .vector1 , .vector2 , .vector3 {
        display: none;
    }

    .path__box {
        margin: 1.5rem;
    }
}
.inner__footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 20px;
}

.footer__box {
    height: 220px;
    width: 200px;
    margin: 2px 3rem 2px 1rem;
}

.footer__box > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FF8400;
}

.footer__box > p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #2B2D42;
    margin: 0;
    padding: 3px 0px;
}

.double__box {
    display: -webkit-flex;
    display: flex;
}

input {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: rgba(26, 32, 61, 0.3);
    padding: 0.8rem;
    background: #FCFCFD;
    border: 1px solid #CFD0D7;
}

.conn {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.conn > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 172.4%;
    color: #2B2D42;
}

.footer__box > .btn {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 128px;
    height: 42px;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    margin: 0.5rem 0rem;
}

.logos > img {
    width: 60px;
    height: 60px;
    padding: 1px 4px;
}

.logos {
    display: -webkit-flex;
    display: flex;
    padding: 6px 0px;
}

.footer__box > .logo {
    margin: 0;
    padding: 0 0.1rem;
    width: 150px;
    height: 80px;
}

.footer__box > p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #5F6377;
}

.links > i {
    color: #0047AB;
    font-size: 20px;
    padding: 0 3px;
}

@media (max-width: 980px) {
    .inner__footer {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .footer__box , .double__box{
        width: 90%;
        height: 100%;
    }

    .double__box > .footer__box {
        padding: 0;
        margin: 0;
    }

    .footer__box {
        padding-left: 2.2rem;
    }
}
.copyryt__sec {
    padding: 1.5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-top: 1px solid #C4C4C4;
    margin-top: 3rem;
}

.copyryt__sec > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 172.4%;
    color: #2B2D42;
}

.copyryt__sec > h1 > span {
    color: #FF8B08;
}

@media (max-width: 980px) {
    .copyryt__sec {
        margin: 1rem 0rem;
    }
}
